@charset "UTF-8";.ecl-blockquote{-webkit-border-start:8px solid #0e47cb;-webkit-padding-start:1rem;border-end-start-radius:4px;border-inline-start:8px solid #0e47cb;border-start-start-radius:4px;margin:0;padding-bottom:.75rem;padding-inline-start:1rem;padding-top:.75rem}.ecl-blockquote__citation{color:#515560;font:normal normal 400 1.125rem/1.75rem arial,sans-serif;font-style:italic;margin:0;max-width:65ch;quotes:"“" "”"}.ecl-blockquote__citation:before{content:open-quote}.ecl-blockquote__citation:after{content:close-quote}.ecl-blockquote__quote{margin:0}.ecl-blockquote__attribution{margin-top:1rem;max-width:65ch}.ecl-blockquote__author{color:#515560;font:normal normal 400 .875rem/1.25rem arial,sans-serif;font-weight:400}.ecl-blockquote__image{aspect-ratio:1/1;height:120px;margin-top:.75rem;-o-object-fit:cover;object-fit:cover;width:120px}@media (min-width:480px){.ecl-blockquote{-webkit-padding-start:1.5rem;padding-bottom:1rem;padding-inline-start:1.5rem;padding-top:1rem}.ecl-blockquote__citation{font:normal normal 400 1.5rem/1.75rem arial,sans-serif;font-style:italic}.ecl-blockquote__attribution{margin-top:1.5rem}.ecl-blockquote__author{font:normal normal 400 1rem/1.5rem arial,sans-serif;font-weight:400}}@media (min-width:996px){.ecl-blockquote{-webkit-border-start:0;border-inline-start:0;display:flex;flex-direction:row-reverse;justify-content:start;padding:0}.ecl-blockquote__body{-webkit-border-start:8px solid #0e47cb;-webkit-padding-start:1.5rem;border-end-start-radius:4px;border-inline-start:8px solid #0e47cb;border-start-start-radius:4px;display:inline-block;padding-bottom:1rem;padding-inline-start:1.5rem;padding-top:1rem}.ecl-blockquote__citation{font:normal normal 400 1.5rem/1.75rem arial,sans-serif;font-style:italic}.ecl-blockquote__attribution{margin-top:1.5rem}.ecl-blockquote__author{font:normal normal 400 1.125rem/1.75rem arial,sans-serif;font-weight:400}.ecl-blockquote__image{-webkit-margin-end:1.5rem;margin-inline-end:1.5rem;margin-top:0}}